import React from 'react';
import { Bar, Doughnut, Line, Pie, Polar, Radar, Bubble } from 'react-chartjs-2';
import { CustomTooltips } from '@coreui/coreui-plugin-chartjs-custom-tooltips';

import axios from 'axios';
const options = {
  tooltips: {
    enabled: false,
    custom: CustomTooltips
  },
  maintainAspectRatio: false
}

export default class BarChart extends React.Component {
  state = {
    bar: {
      labels: [],
      datasets: [
        {
          label: 'My First dataset',
          backgroundColor: 'rgba(255,99,132,0.2)',
          borderColor: 'rgba(255,99,132,1)',
          borderWidth: 1,
          hoverBackgroundColor: 'rgba(255,99,132,0.4)',
          hoverBorderColor: 'rgba(255,99,132,1)',
          data: [],
       
        }
        
      ],
    }
  }

  componentWillMount() {
    let url = 'http://10.22.1.134/EficazRTChartsAPI/Api/Charts/GetChartsData'
    axios.get(url)
      .then(res => {
        const persons = res.data;
        console.log("Response")
        console.log(res)
        // this.setState({ persons });
        let Names = []
        let Durations = []
        res.data.recentExtractions.map((item, i) => {
          // if (i < 3) {
            Names.push(item.DeName)
            Durations.push(item.Duration)
          // }
        })
        // this.setState({ pie:{labels:Names}});
        this.setState(state => {
          state.bar.datasets[0].data = Durations;
          state.bar.labels = Names
          return state
        });
      })
  }

  render() {
    console.log("pie renders")
    console.log(this.state.bar)
    return (
      <Bar data={this.state.bar} options={options}/>

    )
  }
}