export default class PieChart extends React.Component {
  state = {
    pie: {
      labels: [
        // 'Red',
        // 'Green',
        // 'Yellow',
      ],
      datasets: [
        {
          data: [
            // 15, 15, 15
          ],
          backgroundColor: [
            '#FF6384',
            '#36A2EB',
            '#FFCE56',
          ],
          hoverBackgroundColor: [
            '#FF6384',
            '#36A2EB',
            '#FFCE56',
          ],
        }
      ],
    }
  }

  componentWillMount() {
    let url = 'http://10.22.1.134/EficazRTChartsAPI/Api/Charts/GetChartsData'
    axios.get(url)
      .then(res => {
        const persons = res.data;
        console.log("Response")
        console.log(res)
        // this.setState({ persons });
        let Names = []
        let Durations = []
        res.data.recentExtractions.map((item, i) => {
          if (i < 3) {
            Names.push(item.DeName)
            Durations.push(item.Duration)
          }
        })
        // this.setState({ pie:{labels:Names}});
        this.setState(state => {
          state.pie.datasets[0].data = Durations;
          state.pie.labels = Names
          return state
        });
      })
  }

  render() {
    console.log("pie renders")
    console.log(this.state.pie)
    return (
      <Pie data={this.state.pie} />

    )
  }
}